.Message {
  background-color: white;

  display: flex;
  justify-content: flex-end;
  padding: 2px 5%;
  margin-top: 3px;
  .messageBox {
    background: #f3f3f3;
    border-radius: 20px;
    padding: 0px 20px;
    color: white;
    display: inline-block;
    max-width: 80%;

    .messageText {
      width: 100%;
      letter-spacing: 0;
      float: left;
      font-size: 1em;
      word-wrap: break-word;
      background: none;
    }
    .colorWhite {
      color: white;
    }
    .colorDark {
      color: #353535;
    }
  }

  .messageText img {
    vertical-align: middle;
  }

  .sentText {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: #828282;
    letter-spacing: 0.3px;
    background: none;

  }
  .pl-10 {
    padding-left: 10px;
  }
  .pr-10 {
    padding-right: 10px;
  }

  .backgroundBlue {
    background: #2979ff;
  }
  .backgroundLight {
    background: #f3f3f3;
  }
}

.justifyStart {
  justify-content: flex-start;
}
.justifyEnd {
  justify-content: flex-end;
}
